// ----------------------------------------------------------------------

const StyledPaper = styled(Paper)`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 12px;
`;

const Title = styled(Typography)`
  margin-bottom: 12px;
  font-weight: bold;
`;

const Content = styled(Typography)`
  margin-bottom: 12px;
  color: #666;
`;
const NameContent = styled(Typography)`
  margin-bottom: 120px;
  color: #666;
`;

const StyledButton = styled(Button)`
  margin-top: auto;
  box-shadow: none;
  background-color: blue; /* You can change this color */
  color: #fff; /* Text color for the button */
  &:hover {
    background-color: lightblue; /* Change color on hover */
  }
`;

const CardComponent = ({ id, token, title, content, buttonText, name }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleClick = async () => {
    try {
      await dispatch(getDomainList(id, token));
      await dispatch(getCollectionList(id, token));
      navigate(PATH_DASHBOARD.general.collection);
    } catch (error) {
      console.error("API error:", error);
    }
  };
  // dispatch(getitemList(token, collectionIds));

  return (
    <>
      <StyledPaper elevation={3}>
        <Title variant="h6">{title}</Title>
        <Content variant="body1">{content}</Content>
        <Stack
          direction={{ xs: "column", sm: "row" }}
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
          }}
          spacing={2}
        >
          <StyledButton
            variant="contained"
            color="primary"
            onClick={handleClick}
          >
            {buttonText}
          </StyledButton>
          <NameContent
            variant="body1"
            sx={{
              display: "flex",
              flexDirection: "row",
              alignContent: "center",
              alignItems: "center",
            }}
          >
            <MUIAvatar
              sx={{ width: 30, height: 30, marginRight: "5px" }}
            ></MUIAvatar>
            {name}
          </NameContent>
        </Stack>
      </StyledPaper>
    </>
  );
};
export default function GeneralApp() {
  const dispatch = useDispatch();
  const user = useSelector((state) => state?.wfUser?.user?.data?.data?.user);
  useEffect(async () => {
    await dispatch(getwfUserDetails());
  }, []);
  const userDetails = useSelector((state) => state?.wfUser?.userDetails?.data);

  useEffect(() => {
    const fetchData = async () => {
      try {
        // First, get the user details
        const queryParams = new URLSearchParams(window.location.search);
        if (queryParams.has("code")) {
          const code = queryParams.get("code");
          const res1 = await dispatch(getwfUser(code));
          if (res1.success == true) {
            const token = localStorage.getItem("token");
            const res2 = await dispatch(getSites(token));
            if (res2.success == true) {
              await dispatch(getwfUserDetails());
            }
          }
        } else {
          console.error("Code parameter not found in the URL");
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, [dispatch]);

  const { themeStretch } = useSettings();
  const handleAddSite = () => {
    const authorizationUrl = `https://webflow.com/oauth/authorize?response_type=code&client_id=${process.env.REACT_APP_CLIENT_ID}`;
    window.location.href = authorizationUrl;
  };

  const { enqueueSnackbar } = useSnackbar();

  const error = useSelector((state) => state.category.error);
  useEffect(() => {
    if (error) {
      enqueueSnackbar(error.message, { variant: "error" });
    }
  }, [error]);
  return (
    <Page title="General: App">
      <Container maxWidth={themeStretch ? false : "xl"}>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginBottom: "16px",
          }}
        >
          <Button
            key="addsite"
            onClick={handleAddSite}
            color="inherit"
            variant="outlined"
            size="large"
            sx={{
              m: 0.5,
              flexShrink: 0,
              color: "white",
              bgcolor: "blue",
              "&:hover": {
                borderColor: "lightblue",
                bgcolor: "lightblue",
              },
            }}
          >
            + Add Site
          </Button>
        </div>
        <Grid container spacing={3}>
          {userDetails?.data?.map((data, index) =>
            data.userSites?.map((userSite, siteIndex) => {
              const userDetails = data.userDetails.firstname;
              return (
                <Grid
                  item
                  xs={12}
                  md={4}
                  key={`${index}-${siteIndex}`}
                  style={{ height: "250px" }}
                >
                  <CardComponent
                    token={data.userDetails.wf_access_token}
                    id={userSite.wf_id}
                    title={userSite.name}
                    content={userSite.shortName}
                    buttonText="View"
                    name={userDetails}
                  />
                </Grid>
              );
            })
          )}
        </Grid>
      </Container>
    </Page>
  );
}
